
.login {
    width: 400px;
    background: #fff;
    border: 1px solid #dddfe2;
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
    border-radius: 8px;
    padding: 1rem;
    align-items: center;
    text-align: center;
    margin-left: 35%;
}

.login > input {
    border-radius: 8px;
    border: 2px solid #dddfe2;
    outline: none;
    color: #1d2129;
    margin: 0.5rem 0;
    padding: 0.5rem 0.75rem;
    width: 92%;
    font-size: 1rem;
}

.login .button {
    text-decoration: none;
    background: #1877f2;
    border: 1px solid #1877f2;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    font-weight: bold;
}
.login .button a{
    text-decoration: none;
    color: #fff;
    transition: all 0.2s ease-in-out;
}
.login .button:hover{
    transition: all 0.2s ease-in-out;
   background: #fff;
   color: #1877f2;
}
.login .button a:hover{
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #1877f2;
 }

 @media only screen and (max-width: 1100px){
    .login{
      transition: all 0.45s;
      width: 350px;
    }
  }      

  @media only screen and (max-width: 950px){
    .login{
        transition: all 0.45s;
        width: 250px;
      }
  }      