* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0;
    background:linear-gradient(to left,rgb(6, 97, 76), rgb(132, 221, 203));
    /* background-image: url(https://th.bing.com/th/id/R.88cf81e3d1f2d6185f94662d568d3318?rik=u%2fS1jDxdD%2fxVVg&pid=ImgRaw&r=0); */
    width: 100%;
    height: 100%;
  }
  
  .Parallax {
    display: flex;
    justify-content:center;
  }
    .background,
    .background-triangles {
      width: 80%;
      height: 1000px;
      margin-top: 4%;
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      margin-right: 5%;
    }
  
    .background {
      background-image: url("https://th.bing.com/th/id/R.a770575de4499716c259d3363d425fa5?rik=yWE12f4yHUPsAw&riu=http%3a%2f%2fwww.pngall.com%2fwp-content%2fuploads%2f2016%2f07%2fTechnology-Free-PNG-Image.png&ehk=KG9KxiINCEmuojg2ll37m8YuFH4MDdVXweMl5z8reto%3d&risl=&pid=ImgRaw&r=0")
    }
  
     .background-triangles {
      /* background-image: url("https://payload.cargocollective.com/1/7/245400/12594118/Virus-smaller.gif"); */
      /* height: 400px; */
      display: flex;
      width: 20%;
      margin-top: 25%;

    } 

    .content {
      width: 1000px;
      display: flex;
      flex-direction: column;
      align-items: center;
      z-index: 1;
      margin-top: 140px;
      /* margin-left: 20%; */
    }
    .heading,
    .cta {
        text-align: center;
        display: flex;
        flex-direction: column;
        margin-left: 65%;
      }
      .cta{
        margin-left: 62%
      }
      .heading {
        width: 80%;
        color: rgb(0, 62, 48);
      }
      .text {
          margin: 10px 0;
          font-size: 62px;
        }
  
        .caption {
          margin: 0;
          font-size: 25px;
          text-align: center;
          color: rgb(255, 255, 255);
        }

  
      .cta {
        background-color: transparent;
        width: 70%;
        padding: 40px;
        font-size: 20px;
        color: rgb(255, 255, 255);
        align-items: left;
        text-align: left;
        /* margin-top: 80px; */
      }


      @media only screen and (max-width: 1100px){
        .text{
          font-size: 50px;
          transition: all 0.45s;
        }
        .caption{
          font-size: 18px;
          transition: all 0.45s;
        }
        .cta{
          font-size: 15px;
          transition: all 0.45s;
        }
        body{
          width: 80%;
        }
      }      

      @media only screen and (max-width: 950px){
        .text{
          font-size: 40px;
          transition: all 0.45s;
        }
        .caption{
          font-size: 15px;
          transition: all 0.45s;
        }
        .cta{
          font-size: 13px;
          transition: all 0.45s;
        }
      }      